<template>
  <div>
    <base-header>
      <div class="row align-items-center py-4">
        <div class="col-lg-6 col-7">
          <h6 class="h2 text-white d-inline-block mb-0">공지사항</h6>
          <nav aria-label="breadcrumb" class="d-none d-md-inline-block ml-md-4">
            <route-bread-crumb></route-bread-crumb>
          </nav>
        </div>
      </div>
    </base-header>
    <div class="container-fluid mt-4">
      <div class="row">
        <div class="col-12">
          <card>
            <!-- Card header -->
            <template v-slot:header>
              <div class="row align-items-center">
                <div class="col-10">
                  <h6 class="surtitle"># 공지</h6>
                  <h5 class="h3 mb-0">{{ post.title }}</h5>
                </div>
                <div class="col-2 text-right">
                  <a class="btn btn-sm btn-neutral" @click="fetchPostUpdate(post.id)">수정</a>
                  <a class="btn btn-sm btn-danger" @click="fetchPostDelete(post.id)">삭제</a>
                </div>
              </div>
            </template>
            <!-- Card body -->
            <p class="card-text mb-4">{{ post.content }} <br /></p>
            <router-link to="/notice"><button class="btn btn-primary">뒤로가기</button></router-link>
          </card>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import RouteBreadCrumb from "@/components/Breadcrumb/RouteBreadcrumb";
import axios from "axios";
var getUrl = "/post/";

export default {
  components: {
    RouteBreadCrumb,
  },
  data() {
    return {
      post: {},
    };
  },
  methods: {
    fetchPostDetail(postId) {
      console.log("fetchPostDetail()...", postId);
      axios
        .get(`${getUrl}${postId}/`)
        .then((res) => {
          console.log("POST DETAIL GET RES", res);
          this.post = res.data;
        })
        .catch((err) => {
          console.log("POST DETAIL GET ERR.RESPONSE", err.response);
          alert(err.response.status + " " + err.response.statusText);
        });
    },
    fetchPostDelete(postId) {
      console.log("fetchPostDetail()...", postId);
      axios
        .delete(`${getUrl}${postId}/`)
        .then((res) => {
          console.log("POST DETAIL GET RES", res);
          alert("삭제완료");
          this.$router.push({ name: "Notice" });
        })
        .catch((err) => {
          console.log("POST DETAIL GET ERR.RESPONSE", err.response);
          alert(err.response.status + " " + err.response.statusText);
        });
    },
    fetchPostUpdate(postId) {
      this.$router.push({ name: "NoticeEdit", params: { noticeId: postId } });
    },
  },
  created() {
    // this.fetchPostList();
    console.log(this.$route);
    console.log(this.$route.params.noticeId);
    const postId = this.$route.params.noticeId;
    this.fetchPostDetail(postId);
  },
};
</script>
