import { createRouter, createWebHashHistory } from "vue-router";

import DashboardLayout from "@/views/Layout/DashboardLayout";
import AuthLayout from "@/views/Pages/AuthLayout";

// Dashboard pages
import Dashboard from "../views/Dashboard/Dashboard.vue";
import Charts from "../views/Charts.vue";

// Custom pages
import Notice from "../views/Custom/Notice.vue";
import NoticeDetails from "../views/Custom/NoticeDetails.vue";
import NoticePost from "../views/Custom/NoticePost.vue";
import MyProfile from "../views/Custom/MyProfile.vue";
import MainMenu from "../views/Custom/MainMenu.vue";
import SkillTree from "../views/Custom/SkillTree.vue";
import Blog from "../views/Custom/Blog.vue";

// Pages
import Pricing from "../views/Pages/Pricing.vue";
import Login from "../views/Pages/Login.vue";
import Register from "../views/Pages/Register.vue";
import Lock from "../views/Pages/Lock.vue";
import Profile from "../views/Pages/UserProfile.vue";
import Timeline from "../views/Pages/TimeLinePage.vue";

const routes = [
  {
    path: "/",
    redirect: "/",
    component: DashboardLayout,
    name: "Dashboards",
    children: [
      {
        path: "/",
        name: "MainMenu",
        components: { default: MainMenu },
      },
      {
        path: "/myprofile",
        name: "MyProfile",
        components: { default: MyProfile },
      },
      {
        path: "/skilltree",
        name: "SkillTree",
        components: { default: SkillTree },
      },
      {
        path: "/blog",
        name: "Blog",
        components: { default: Blog },
      },
      {
        path: "/notice",
        name: "Notice",
        components: { default: Notice },
      },
      {
        path: "/notice/post",
        name: "NoticePost",
        components: { default: NoticePost },
      },
      {
        path: "/notice/:noticeId",
        name: "NoticeDetails",
        components: { default: NoticeDetails },
        props: true,
      },
      {
        path: "/notice/edit/:noticeId",
        name: "NoticeEdit",
        components: { default: NoticePost },
        props: true,
      },
      {
        path: "/dashboard",
        name: "Dashboard",
        components: { default: Dashboard },
      },
      {
        path: "/charts",
        name: "Charts",
        components: { default: Charts },
      },
      {
        path: "/pages/user",
        name: "Profile",
        components: { default: Profile },
      },
      {
        path: "/pages/timeline",
        name: "Timeline",
        components: { default: Timeline },
      },
    ],
  },
  {
    path: "/",
    redirect: "/",
    component: AuthLayout,
    children: [
      {
        path: "/pricing",
        name: "Pricing",
        components: { default: Pricing },
      },
      {
        path: "/login",
        name: "Login",
        components: { default: Login },
      },
      {
        path: "/register",
        name: "Register",
        components: { default: Register },
      },
      {
        path: "/lock",
        name: "Lock",
        components: { default: Lock },
      },
    ],
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  linkActiveClass: "active",
  routes,
  scrollBehavior(to, from, savedPosition) {
    return { x: 0, y: 0 };
  },
});

export default router;
