<script>
import Chart from "chart.js";
import ChartjsGague from "chartjs-gauge";

function randomScalingFactor() {
  return Math.round(Math.random() * 100);
}

export default {
  components: {},
  data() {
    return {
      salesChartID: "salesChart",
      gaugeChartID: "gaugeChartID",
      gaugeChartID2: "gaugeChartID2",
    };
  },
  methods: {
    createChart: function (chartId) {
      const ctx = document.getElementById(chartId).getContext("2d");

      new Chart(ctx, {
        type: "line",
        data: {
          labels: ["May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"],
          datasets: [
            {
              label: "Performance",
              tension: 0.4,
              borderWidth: 4,
              borderColor: "#5e72e4",
              pointRadius: 0,
              backgroundColor: "transparent",
              data: [0, 20, 10, 30, 15, 40, 20, 60, 60],
              barPercentage: 1.6,
            },
          ],
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          legend: {
            display: false,
          },
          tooltips: {
            enabled: true,
            mode: "index",
            intersect: false,
          },
          scales: {
            yAxes: [
              {
                gridLines: {
                  drawBorder: false,
                  color: "transparent",
                  zeroLineColor: "transparent",
                },
                ticks: {
                  padding: 0,
                  fontColor: "#8898aa",
                  fontSize: 13,
                  fontFamily: "Open Sans",
                },
              },
            ],
            xAxes: [
              {
                gridLines: {
                  drawBorder: false,
                  color: "rgba(29,140,248,0.0)",
                  zeroLineColor: "transparent",
                },
                ticks: {
                  padding: 10,
                  fontColor: "#8898aa",
                  fontSize: 13,
                  fontFamily: "Open Sans",
                },
              },
            ],
          },
          layout: {
            padding: 0,
          },
        },
      });
    },
    createGaugeChart(chartId) {
      const ctx = document.getElementById(chartId).getContext("2d");

      new Chart(ctx, {
        type: "doughnut",
        data: {
          labels: ["Danger", "Info"],
          datasets: [
            {
              label: "Dataset 1",
              tension: 0.4,
              data: [randomScalingFactor(), randomScalingFactor()],
              backgroundColor: ["#f5365c", "#11cdef"],
              barPercentage: 1.6,
            },
          ],
        },
        options: {
          circumference: Math.PI * 1.5,
          rotation: Math.PI * 0.75,
          responsive: true,
          maintainAspectRatio: false,
          cutoutPercentage: 50,
          plugins: {
            doughnutlabel: {
              labels: [
                {
                  text: "The title",
                  font: {
                    size: "60",
                  },
                },
              ],
            },
          },
        },
      });
    },
    createGaugeChart2: function (chartId) {
      var ctx = document.getElementById(chartId).getContext("2d");
      new ChartjsGague(ctx, {
        type: "gauge",
        data: {
          datasets: [
            {
              value: 3.5,
              minValue: 0,
              data: [1, 2],
              backgroundColor: ["#f5365c", "#11cdef"],
            },
          ],
        },
        options: {
          responsive: false,
          needle: {
            radiusPercentage: 0,
            widthPercentage: 0,
            lengthPercentage: 80,
            color: "rgba(0, 0, 0, 1)",
          },
          valueLabel: {
            display: true,
            formatter: (value) => {
              return Math.round(value) + "0%";
            },
            color: "rgba(255, 255, 255, 1)",
            fontSize: "15",
            backgroundColor: "rgba(0, 0, 0, 1)",
            borderRadius: 5,
            padding: {
              top: 10,
              bottom: 10,
            },
          },
        },
      });
    },
  },
  mounted() {
    this.createChart(this.salesChartID);
    this.createGaugeChart(this.gaugeChartID);
    this.createGaugeChart2(this.gaugeChartID2);
  },
};
</script>
