<template>
  <div>
    <base-header>
      <div class="row align-items-center py-4">
        <div class="col-lg-6 col-7">
          <h6 class="h2 text-white d-inline-block mb-0">공지사항 작성</h6>
          <nav aria-label="breadcrumb" class="d-none d-md-inline-block ml-md-4">
            <route-bread-crumb></route-bread-crumb>
          </nav>
        </div>
      </div>
    </base-header>
    <div class="container-fluid mt-4">
      <div class="row">
        <div class="col-12">
          <card>
            <!-- Card header -->
            <template v-slot:header>
              <div class="row align-items-center">
                <div class="col-10">
                  <!-- Title -->
                  <h5 class="h3 mb-0">공지사항 {{ action }}</h5>
                </div>
                <div class="col-2 text-right">
                  <button class="btn btn-sm btn-neutral" @click="fetchPostRegist(action)">{{ action }}</button>
                  <button class="btn btn-sm btn-danger" @click="cancle">취소</button>
                </div>
              </div>
            </template>
            <!-- Card body -->
            <form>
              <div class="form-group">
                <label for="exampleFormControlInput1">제목</label>
                <input type="text" class="form-control" v-model="post.title" />
              </div>
              <div class="form-group">
                <label for="exampleFormControlTextarea1">내용</label>
                <textarea class="form-control" rows="16" v-model="post.content"></textarea>
              </div>
              <div class="form-group">
                <label for="exampleFormControlTextarea1">카테고리</label>
                <input type="text" class="form-control" v-model="post.category" />
              </div>
            </form>
          </card>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import RouteBreadCrumb from "@/components/Breadcrumb/RouteBreadcrumb";
import axios from "axios";
var getUrl = "/post/";

export default {
  components: {
    RouteBreadCrumb,
  },
  data() {
    return {
      id: "",
      post: { title: "", content: "", category: "" },
      action: "",
    };
  },
  methods: {
    fetchPostRegist(action) {
      if (action == "수정") {
        axios
          .put(getUrl + this.id + "/", this.post)
          .then((res) => {
            console.log("POST LIST GET RES", res);
            alert("수정완료");
            this.$router.push({ name: "Notice" });
          })
          .catch((err) => {
            console.log("수정에러 GET ERR.RESPONSE", err.response);
            alert(err.response.status + " " + err.response.statusText);
          });
      } else {
        axios
          .post(getUrl, this.post)
          .then((res) => {
            console.log("POST LIST GET RES", res);
            alert("등록완료");
            this.$router.push({ name: "Notice" });
          })
          .catch((err) => {
            console.log("등록 에러 GET ERR.RESPONSE", err.response);
            alert(err.response.status + " " + err.response.statusText);
          });
      }
    },
    fetchPostDetail(postId) {
      console.log("fetchPostDetail()...", postId);
      axios
        .get(`${getUrl}${postId}/`)
        .then((res) => {
          console.log("POST DETAIL GET RES", res);
          this.post = res.data;
        })
        .catch((err) => {
          console.log("POST DETAIL GET ERR.RESPONSE", err.response);
          alert(err.response.status + " " + err.response.statusText);
        });
    },
    cancle() {
      //뒤로가기
      this.$router.push({ name: "Notice" });
    },
  },
  created() {
    this.id = this.$route.params.noticeId;
    if (this.id) {
      this.fetchPostDetail(this.id);
      console.log("update");
      this.action = "수정";
    } else {
      console.log("create");
      this.action = "등록";
    }
  },
};
</script>
